<template>
	<div class="store" ref="homePage">
		<Header :title="msg"></Header>
		<div class="part1">
			<van-row class="store_title">
				<van-col  :span="6" class="store_title_baseinfo">基本信息</van-col>
			</van-row>
			<van-row class="store_name">
				<van-col  :span="8">店铺名称<i>*</i></van-col>
				<van-col  :span="16">
					<input type="text" placeholder="请输入店铺名字" v-model="storeName">
				</van-col>
			</van-row>
			<van-row class="contact_name">
				<van-col  :span="8">联系人姓名<i>*</i></van-col>
				<van-col  :span="16">
					<input type="text" placeholder="请输入真实姓名" v-model="contactName">
				</van-col>
			</van-row>
			<van-row class="contact_number">
				<van-col  :span="8">联系人电话<i>*</i></van-col>
				<van-col  :span="16">
					<input max="11" type="number" placeholder="请输入联系人电话" v-model="contactNumber">
				</van-col>
			</van-row>
			<van-row class="store_address">
				<router-link to="/choosecity">
					<van-col  :span="9">门店地址<i>*</i></van-col>
					<van-col  :span="2" :offset="13">
						<van-icon name="arrow"></van-icon>
					</van-col>
				</router-link>
			</van-row>
			<van-row class="store_location">
				<router-link to="/chooselo">
					<van-col  :span="9">标记店铺位置</van-col>
					<van-col  :span="2" :offset="13">
						<van-icon name="arrow"></van-icon>
					</van-col>
				</router-link>
			</van-row>
			<van-row class="store_detail_address">
				<van-col :span="24">
					<input type="text" :placeholder="detailLocation" v-model="detailLocation">
				</van-col>
			</van-row>
		</div>
		<div class="part2">
			<van-row class="store_title">
				<van-col  :span="6">门店照片</van-col>
			</van-row>
			<van-row  class="store_front">
				<van-col  :span="7">门脸照<i>*</i></van-col>
				<van-col  :span="5" :offset="1">
					<van-uploader :after-read="onRead1" ref="storeFront" accept="image/gif,image/jpeg,image/png">
					  <van-icon :name="imgPath1" :class="{active:storeFrontPhoto == true}"/>
					</van-uploader>
				</van-col>
				<van-col  :span="10">
					<p>一张真实美观的门脸照可以 提升店铺形象</p>
					<p @click="showDialog1">查看示例图</p>
				</van-col>
			</van-row>
			<van-row  class="store_inside">
				<van-col  :span="7">店内照<i>*</i></van-col>
				<van-col  :span="5" :offset="1">
					<van-uploader :after-read="onRead2" ref="storeFront" accept="image/gif,image/jpeg,image/png">
					  <van-icon  :name="imgPath2" :class="{active:storeInsidePhoto== true}"/>
					</van-uploader>
				</van-col>
				<van-col  :span="10">
					<p>简介干净的店内设施可以让用户放心舒心</p>
					<p @click="showDialog2">查看示例图</p>
				</van-col>
			</van-row>
			<van-row  class="store_logo">
				<van-col  :span="7">店铺LOGO<i>*</i></van-col>
				<van-col  :span="5" :offset="1">
					<van-uploader :after-read="onRead3" ref="storeFront" accept="image/gif,image/jpeg,image/png">
					  <van-icon :name="imgPath3" :class="{active:storeLogo== true}"/>
					</van-uploader>
				</van-col>
				<van-col  :span="10">
					<p>店铺LOGO(支持jpg、jpeg、png格式,大小不超过1MB)</p>
					<p @click="showDialog3">查看示例图</p>
				</van-col>
			</van-row>
		</div>
			<van-row   class="store_btn_next">
				<van-col :span="22"  :offset="1">
					<van-button @click="next" type="info" size="large">下一步</van-button>
				</van-col>
			</van-row>
	</div>
</template>

<script>
	import Header from '../header'
	import bus from '../../assets/js/eventBus.js'
	export default {
		data(){
			return {
				msg:"1/3 店铺信息",
				storeName:"",
				contactName:"",
				contactNumber:"",
				chooseLocation:"",
				tagLocation:"",
				detailLocation:"详细地址 例：1号楼101室",
				dialogImageUrl: '',
				dialogVisible: false,
				clientHeight:"",
				regExpAll:true,
				file:null,
				storeFrontImage:"plus",
				storeFrontPhoto:false,
				storeInsidePhoto:false,
				storeLogo:false,
				imgPath1:"plus",
				imgPath2:"plus",
				imgPath3:"plus",
				member_id:parseInt(Math.random()*10),
				// imgPath:"http://pqk40fvkr.bkt.clouddn.com/",
			}
		},
		components:{
			Header,
			
		},
		mounted(){
			this.$nextTick(function(){
				bus.$on("getChooseCityValue",(msg)=>{
					this.tagLocation = msg;
				});
				bus.$on("getChooseLocationValue",(msg)=>{
					this.detailLocation = msg;
				});
			})
		},
		beforeDestory(){
			bus.$off("getChooseCityValue");
			bus.$off("getChooseLocationValue");
		},
		methods:{
			showDialog1(){
				console.log(this.taglocation);
				this.$dialog.alert({
				  title: '门脸照示意图',
				  message: `<img src="/static/img/x2/icon_bnt_dpzp@2x.png"><p>门店照需拍出完整门匾，门框，需在店铺开门营业状态下完成拍摄</p>`,
				  confirmButtonText:"知道了"
				})
			},
			showDialog2(){
				this.$dialog.alert({
				  title: '店内照示意图',
				  message: `<img src="/static/img/x2/icon_bnt_dnzp@2x.png"><p>店内照片需要实际反映内部情况（比如收银台、餐桌椅等）</p>`,
				  confirmButtonText:"知道了"
				})
			},
			showDialog3(){
				this.$dialog.alert({
				  title: 'LOGO示意图',
				  message: `<img src="/static/img/x2/shilitu_bnt_klogo@2x.png"><p>店铺LOGO（支持jpg、jprg、png格式，大小不超过1mb）</p>`,
				  confirmButtonText:"知道了"
				  })
			},
			onRead1(file) {
				if(file.file.size>=1048576){
					this.$toast("图片大小不能超过2M");
					this.imgPath1 = "plus";
				}else{
					var formData = new FormData();
					formData.append("file",file.file);
					let config = {
						headers: {"Content-Type":"multipart/form-data"}
					}
					this.$axios.post("/v3/image_upload",formData,config)
					.then((data)=>{
					this.imgPath1="http://pqk40fvkr.bkt.clouddn.com/"+data.data.data;
					// this.imgPath1="http://127.0.0.1:80/"+data.data.path;
					this.storeFrontPhoto = true;
					})
				}
			},
			onRead2(file) {
				if(file.file.size>=1048576){
					this.$toast("图片大小不能超过2M");
					this.imgPath2 = "plus";
				}else{
					var formData = new FormData();
					formData.append("file",file.file);
					let config = {
						headers: {"Content-Type":"multipart/form-data"}
					}
					this.$axios.post("/v3/image_upload",formData,config).then((data)=>{
					this.imgPath2="http://pqk40fvkr.bkt.clouddn.com/"+data.data.data;
					this.storeInsidePhoto = true;
					})
				}
			},
			onRead3(file) {
				if(file.file.size>=1048576){
					this.$toast("图片大小不能超过2M");
					this.imgPath3 = "plus";
				}else{
					var formData = new FormData();
					formData.append("file",file.file);
					let config = {
						headers: {"Content-Type":"multipart/form-data"}
					}
					this.$axios.post("/v3/image_upload",formData,config).then((data)=>{
					this.imgPath3="http://pqk40fvkr.bkt.clouddn.com/"+data.data.data;
					this.storeLogo = true;
					})
				}
			},
			choosecity(){
				this.$router.push('/choosecity');
			},
			chooseLocationClick(){
				this.$router.push('/chooselo');
			},
			next(){
				let checkStoreName = /^[\u4e00-\u9fa5A-Za-z0-9-_]{1,30}$/;
				let checkcontactName = /^[\u4e00-\u9fa5A-Za-z0-9-_]{1,30}$/;
				var regexpLocation =  /^([\u4e00-\u9fa5a-zA-Z0-9]{0,50})$/;
				let checkPhone = /\d{11}/;
				if(!checkStoreName.test(this.storeName)||this.storeName==""){
					this.$toast("请检查店铺名字是否输入错误或为空");
				}else{
					if(!checkcontactName.test(this.contactName)||this.contactName==""){
						this.$toast("请检查联系人姓名是否输入错误或为空");
					}else{
						if(!checkPhone.test(this.contactNumber)||this.contactNumber==""){
							this.$toast("请检查联系人电话是否输入错误或为空");
						}else{
							if(!regexpLocation.test(this.tagLocation)||this.tagLocation==""){
								this.$toast("请检查门店地址是否选择");
								console.log(this.tagLocation);
								console.log(this.detailLocation);
							}else{
								if(!regexpLocation.test(this.detailLocation)||this.detailLocation==""){
									this.$toast("请检查位置是否标记或输入正确");
									// console.log(this.detailLocation);
								}else{
									if(this.imgPath1=="plus"||this.imgPath2=="plus"||this.imgPath3=="plus"){
										this.$toast("请检查图片是否上传");
										
										// this.$router.push('/ident');
									}else{
										this.$router.push('/ident');
										sessionStorage.setItem("member_id",this.member_id);
										sessionStorage.setItem("company_name",this.storeName);
										sessionStorage.setItem("contacts_name",this.contactName);
										sessionStorage.setItem("contacts_phone",this.contactNumber);
										sessionStorage.setItem("company_address",this.taglocation);
										sessionStorage.setItem("company_address_detail",this.detailLocation);
										sessionStorage.setItem("face_img",this.imgPath1);
										sessionStorage.setItem("store_img",this.imgPath2);
										sessionStorage.setItem("logo_img",this.imgPath3);
									}
								}
							}
						}
					}
				}
				//next justice
			}
		}
	}
</script>
